import React from 'react';
import { Modal } from 'antd-mobile';
import downloadIcon from '../assets/download_icon.png';

const download = () => {
  _hmt.push(['_trackEvent', '', '', 'FM']);
  window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.yibasan.lizhifm&g_f=991784#opened';
};

const DownloadDialog = (props) => {
  const { showDialog, onClose } = props;
  return (
    <Modal
      visible={showDialog}
      transparent
      maskClosable={false}
    >
      <div className="download-dialog">
        <img src={downloadIcon} className="download-icon" alt="FM" />
        <div className="ft">
          <div className="btn download" onClick={download}>FM</div>
          <div className="btn cancel" onClick={onClose}></div>
        </div>
      </div>
    </Modal>
  );
};

export default DownloadDialog;



